<template>
  <div>
    <div class="title">
        常用数据
    </div>
    <el-alert
        title="信息提示"
        type="warning"
        :closable="false">
    </el-alert>
    <!-- 这是搜索 -->
    <div class="seach">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="数据项名称">
          <el-input v-model="formInline.user" placeholder="支持正向名称"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 这是tableb表格 -->
    <div class="tabBox">
        <el-table
          :data="tableData"
          stripe
          style="width: 100%">
          <el-table-column
            prop="date"
            label="数据项别名"
            width="180">
          </el-table-column>
          <el-table-column
            prop="name"
            label="数据项名称"
            width="180">
          </el-table-column>
          <el-table-column
            prop="address"
            label="类型">
          </el-table-column>
          <el-table-column
            prop="address"
            label="值">
          </el-table-column>
          <el-table-column
            prop="address"
            label="时间">
          </el-table-column>
          <el-table-column
            prop="address"
            label="操作">
          </el-table-column>
        </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      formInline: {//这是查询输入的内容
        user: ''
      },
      tableData:[{//这是数据表
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }]//这是数据
    }
  },
  methods:{
    onSubmit(){
      console.log('这是点击了查询的页面')
    }
  }
}
</script>

<style lang="stylus" scoped>
.title
  font-size 20px
  color #333
  margin-bottom 20px
  margin-top 20px
  padding-left 15px
  border-left 3px solid #409EFF
.seach
  padding-left 20px
  padding-top 20px
.tabBox
    padding 45px
</style>


